<template>
  <div id="app">
    <div class="header">
      <h4>Characters (w/ spaces): {{charSpaces}}</h4>
      <h1>Word Count: {{wordCount}}</h1>
      <h4>Characters (w/o spaces): {{char}}</h4>
    </div>
    <inpBox class="inpBox"
      v-on:updateInp="updateInp"
      v-on:chkBoxChanged="chkBoxChanged"
      v-bind="{wordCount,chkBox}"
    />
  </div>
</template>

<script>
import inpBox from './components/inpBox.vue'

export default {
  name: 'app',
  components: {
    inpBox
  },
  data() {
    return {
      charSpaces: 0,
      char: 0,
      wordCount: 0,
      inp: "",
      chkBox: false
    }
  },
  methods: {
    arrayRemove(arr, value) {

      return arr.filter(function(ele){
          return ele != value;
      });

    },
    updateInp(oldInput) {
      var s = oldInput;
      s = s.replace( /\u2018|\u2019|\u201A|\uFFFD/g, "'" );
      s = s.replace( /\u201c|\u201d|\u201e/g, '"' );
      s = s.replace( /\u02C6/g, '^' );
      s = s.replace( /\u2039/g, '<' );
      s = s.replace( /\u203A/g, '>' );
      s = s.replace( /\u2013/g, '-' );
      s = s.replace( /\u2014/g, '--' );
      s = s.replace( /\u2026/g, '...' );
      s = s.replace( /\u00A9/g, '(c)' );
      s = s.replace( /\u00AE/g, '(r)' );
      s = s.replace( /\u2122/g, 'TM' );
      s = s.replace( /\u00BC/g, '1/4' );
      s = s.replace( /\u00BD/g, '1/2' );
      s = s.replace( /\u00BE/g, '3/4' );
      s = s.replace(/[\u02DC|\u00A0]/g, " ");

      var newInput = s;
      this.inp = newInput;
      this.calculate()
    },
    chkBoxChanged(newVal){
      this.chkBox =  newVal;

      this.calculate();
    },
    calculate() {
      if (this.chkBox[0]) {
        var count = 0;
        var wordList = this.inp.split('"');
        var strWithoutQuotes = ""
        if (wordList[0] == '') {
          for (var i = 1; i < wordList.length; i++) {
            if (i%2 == 0) {
              strWithoutQuotes += wordList[i];
            }
          }
        } else {
          strWithoutQuotes += wordList[0];
          for (var i = 1; i < wordList.length; i++) {
            if (i%2 == 0) {
              strWithoutQuotes += wordList[i];
            }
          }
        }
        wordList = strWithoutQuotes.split(" ");
          for (var i = 0; i < wordList.length; i++) {
            if (wordList[i] == " " || wordList[i] == "") {
              count++;
            }
          }
          this.charSpaces = strWithoutQuotes.length;
          this.char = this.charSpaces - strWithoutQuotes.split(" ").length + 1;
          this.wordCount = wordList.length - count;
      } else {
        var count = 0;
        var wordList = this.inp.split(" ");
        for (var i = 0; i < wordList.length; i++) {
          if (wordList[i] == " " || wordList[i] == "") {
            count++;
          }
        }
        this.charSpaces = this.inp.length;
        this.char = this.charSpaces - wordList.length + 1;
        this.wordCount = wordList.length - count;
      }
    }
  }
}
</script>

